    --border: 205, 205, 205;
    --bg: 242, 242, 242
}

body {
    font-family: 'Open Sans', sans-serif;
    margin: 10px 200px;
    background-color: rgb(var(--bg));
    cursor: default;
}
h1{
    margin: 5px 0;
    font-size: 18px;
    font-weight: 600
}
.survey-page {
    background-color: white;
    padding: 10px;
    border: 1px solid rgb(var(--border));
    height: 550px

}
.option-item {
    display: flex;
    font-size: 14px;
    align-items: center;
    border-bottom: 1px solid rgba(var(--border), 0.6);
}
.next-button {
    background-color: rgba(123, 140, 157, 1);
    color: white;
    border: none;
    padding: 0 18px;
    
    cursor: pointer;
    font-size: 18px;
}
input[type="checkbox"],
input[type="radio"] {
  width: 18px;
  height: 18px;
  margin: 0 8px 0 0;
}`, "",{"version":3,"sources":["webpack://./src/styled/styles.css"],"names":[],"mappings":"AAEA;IACI,uBAAuB;IACvB;AACJ;;AAEA;IACI,oCAAoC;IACpC,kBAAkB;IAClB,gCAAgC;IAChC,eAAe;AACnB;AACA;IACI,aAAa;IACb,eAAe;IACf;AACJ;AACA;IACI,uBAAuB;IACvB,aAAa;IACb,oCAAoC;IACpC;;AAEJ;AACA;IACI,aAAa;IACb,eAAe;IACf,mBAAmB;IACnB,iDAAiD;AACrD;AACA;IACI,wCAAwC;IACxC,YAAY;IACZ,YAAY;IACZ,eAAe;;IAEf,eAAe;IACf,eAAe;AACnB;AACA;;EAEE,WAAW;EACX,YAAY;EACZ,iBAAiB;AACnB","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Boldonse&family=Montserrat+Underline:ital,wght@0,100..900;1,100..900&family=Mozilla+Text:wght@200..700&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Roboto:ital,wght@0,100..900;1,100..900&family=Winky+Sans:ital,wght@0,300..900;1,300..900&display=swap');\r\n\r\n:root {\r\n    --border: 205, 205, 205;\r\n    --bg: 242, 242, 242\r\n}\r\n\r\nbody {\r\n    font-family: 'Open Sans', sans-serif;\r\n    margin: 10px 200px;\r\n    background-color: rgb(var(--bg));\r\n    cursor: default;\r\n}\r\nh1{\r\n    margin: 5px 0;\r\n    font-size: 18px;\r\n    font-weight: 600\r\n}\r\n.survey-page {\r\n    background-color: white;\r\n    padding: 10px;\r\n    border: 1px solid rgb(var(--border));\r\n    height: 550px\r\n\r\n}\r\n.option-item {\r\n    display: flex;\r\n    font-size: 14px;\r\n    align-items: center;\r\n    border-bottom: 1px solid rgba(var(--border), 0.6);\r\n}\r\n.next-button {\r\n    background-color: rgba(123, 140, 157, 1);\r\n    color: white;\r\n    border: none;\r\n    padding: 0 18px;\r\n    \r\n    cursor: pointer;\r\n    font-size: 18px;\r\n}\r\ninput[type=\"checkbox\"],\r\ninput[type=\"radio\"] {\r\n  width: 18px;\r\n  height: 18px;\r\n  margin: 0 8px 0 0;\r\n}"],"sourceRoot":""}]);
